
:root{
    --primary-color: hsl(199, 100%, 33%);
    --secondary-color: hsl(187, 85%, 43%);
    --gradient-color: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --white-color: hsl(0, 0%, 100%);
    --black-color: hsl(201, 33%, 16%);
    --text-color: hsl(240, 4%, 36%);
    --white-color-cream: hsl(0, 0%, 93%);
    --grey-text: rgb(190, 190, 190);
    --input-color: rgb(239, 239, 239);
    --body-color: rgb(246, 249, 250);
    --card-bg-color: rgb(225, 225, 225);
}

.construction{
    letter-spacing: 1px;

    header{

        background: rgb(43, 66, 77);
    
        img{
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            object-fit: cover;
            display: flex;
            opacity: 0.4;
            max-width: 100%;
            height: auto;
        }
    
        .content{
            text-align: center;
    
        .cardDiv{
            position: absolute;
            top: 15%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: rgba(241, 246, 247, 0.893);
            padding: .7rem;
            border-radius: 1rem;
            font-size: .7rem;

            h1{
                text-transform: uppercase;
            }

            p{
                color: var(--text-color);
            }
        }
    }

    }

    .content{
        text-align: center;
        font-size: .8rem;


        h3{
            margin: 2rem;
            font-weight: 500;
        }
    }

    .quote{
        text-align: center;
        margin: 2rem;
        
        .btn{
            color: var(--white-color);
            margin: 1rem;
        }
    }
}

@media screen and (min-width: 370px) {
    .construction{
        header{
            .content{
                .cardDiv{
                    top: 22%;
                    padding: .3rem;
                    margin: 1rem;
                    font-size: .6rem;

                    h1{
                        margin-bottom: .1rem;
                    }
                }
            }
            
        }
    }
}

@media screen and (min-width: 765px) {
    .construction{
        header{
            .content{
                .cardDiv{
                    top: 25%;
                    padding: 2rem;
                    margin: 1rem;
                    font-size: 1rem;
                }
            }
            
        }
    }
}

@media screen and (min-width: 1020px) {
    .construction{

        header{
            h1{
                letter-spacing: 2px;
                font-size: larger;
            }
            .content{
                .cardDiv{
                    top: 33%;
                    padding: 2rem;
                    margin: 1rem;
                    font-size: 1.3rem;
                }
            }
            
        }
    }
}

@media screen and (min-width: 1440px) {
    .construction{
        header{
            .content{
                .cardDiv{
                    top: 44%;
                }
            }
            
        }
    }
}

@media screen and (min-width: 2560px) {
    .construction{
        header{
            .content{
                .cardDiv{
                    top: 90%;
                }
            }
            
        }
    }

}